<template>
  <div style="padding: 20px 20px;width: calc(100% - 40px);height: calc(100% - 130px); background: #f8f8f8; display: flex; flex-direction: column; padding-top: 5px !important;">
    <div class="table-bar">
      <div style="display: flex; align-items: center;">
        <simple-search :fields="fields" default-search="DRIVER_NAME" @search="setSearch"></simple-search>
      </div>
      <div class="action-button-group">
        <el-button :icon="getPlusIcon" type="primary" @click="openRoute('#/addDriver')">Add driver</el-button>
        <el-button type="danger" @click="deleteFunc">Delete</el-button>
<!--        <el-button type="info" @click="exportFunc">Export</el-button>-->
      </div>
    </div>
    <div style="height: 100%;width: calc(100% - 10px); margin-top: 20px;padding: 5px;background: white;box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;display: flex;flex-direction: column;">
      <div style="display: flex;justify-content: space-between;margin-bottom: 10px;width: 100%;">
        <span style="display: flex; align-items: center;">

        </span>
      </div>
      <list-table :fields="fields"
                  @view="row => openRoute(`#/drivers/view/${row.DRIVER_ID}`)"
                  @edit="row => openRoute(`#/drivers/edit/${row.DRIVER_ID}`)"
                  @multiselect="setMultiselect"
                  :table-data="restData"
                  selectable
                  editable
                  viewable
                  sortable
                  show-index>
      </list-table>
    </div>
  </div>
</template>

<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { Plus, Edit, View } from '@element-plus/icons-vue'
import SimpleSearch from "@/admin/packages/components/search/SimpleSearch";
import CommonMixin from "@/admin/packages/common-mixin";
import ListTable from "@/admin/packages/table/ListTable";
export default {
  name: "ViewDrivers",
  components: {ListTable, SimpleSearch },
  mixins: [CommonMixin],
  data(){
    return {
      p_perPage: 25,
      p_pageNo: 1
    }
  },
  computed: {
    fields(){
      return [
        {
          NAME: "Name",
          VALUE: "DRIVER_NAME",
          clazz: "hello",
          sort: true
        },
        {
          NAME: "Employee Id",
          VALUE: "EMP_ID",
          clazz: "hello",
          sort: false
        },
        {
          NAME: "Phone no",
          VALUE: "PH_NO",
          clazz: "hello",
          sort: true
        },
        {
          NAME: "Distance",
          VALUE: "TOTAL_DISTANCE",
          clazz: "hello",
          sort: false,
          search: false
        },
        {
          NAME: "Minutes",
          VALUE: "TOTAL_MINUTES",
          clazz: "hello",
          sort: true,
          search: false
        },
        {
          NAME: "Cash",
          VALUE: "TOTAL_CASH",
          clazz: "hello",
          sort: false,
          search: false
        },
        {
          NAME: "Digital",
          VALUE: "TOTAL_DIGITAL",
          clazz: "hello",
          sort: false,
          search: false
        },
        {
          NAME: "Rides",
          VALUE: "TOTAL_RIDES",
          clazz: "hello",
          sort: false,
          search: false
        },
      ]
    },
    p_total(){
      return this.restData.length;
    },
    getPlusIcon(){
      return Plus;
    },
    getEditIcon(){
      return Edit;
    },
    getViewIcon(){
      return View;
    }
  },
  methods: {
    openRoute(url){
      window.location.href = url;
    },
    getTableData(){
      const url = "/drivers";
      fetch(url).then(response => response.json())
          .then(res => this.rows = res.DATA);
    },
    deleteFunc(){
      const url = "/deleteDriver";
      console.log(this.multiSelectArray[0]);
      if(this.multiSelectArray.length !== 0){
        ElMessageBox.confirm(
            'Are you sure want to delete?',
            'Warning',
            {
              confirmButtonText: 'OK',
              cancelButtonText: 'Cancel',
              type: 'warning',
            }
        ).then(() => {
          let arrLen = this.multiSelectArray.length;
          let promiseArr = [];
          for(let i = 0; i < arrLen; i++){
            promiseArr.push(fetch(url, { method: "POST",  headers: {'Content-Type': 'application/x-www-form-urlencoded'}, body: "id=" + this.multiSelectArray[i].DRIVER_ID }))
          }
          Promise.all(promiseArr).then(res =>{
                this.getTableData();
                ElMessage({
                  type: 'success',
                  message: 'Successfully deleted!',
                })
              });
              });
      }
      else{
          ElMessage({
            type: 'info',
            message: 'Please select rows to delete',
          })
      }
    },
  },
  mounted() {
    this.getTableData();
  }
}
</script>

