<template>
  <div class="driver-profile" style="padding: 20px; display: flex; flex-direction: column; overflow: scroll; height: calc(100% - 140px)">
    <div style="display: flex;">
      <div>
        <img height="90" width="90" style="border-radius: 50%;" :src="addDriverForm.photo || 'x'" />
      </div>
      <div style="margin-left: 20px; display: flex; align-items: flex-start; flex-direction: column;margin-top: 5px;">
        <div style="font-weight: 700; font-size: 26px;text-align: left;">{{ getDriverName }}</div>
        <div style="font-size: 18px;text-align: left;font-weight: 500;letter-spacing: 1.1px;">{{ addDriverForm.emp_id || "-" }}</div>
      </div>
    </div>
    <div style="display: flex; flex-direction: row; margin-top: 10px; margin-left: 10px;">
      <div class="driver-profile-form-group" style="margin-left: 0 !important;">
        <div class="driver-profile-form">
          <div>Number</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.contactNumber || "-" }}</div>
        </div>

        <div class="driver-profile-form">
          <div>DOB</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.dob || "-" }}</div>
        </div>
      </div>

      <div class="driver-profile-form-group">
        <div class="driver-profile-form">
          <div>Date of joining</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.joinedDate || "-" }}</div>
        </div>

        <div class="driver-profile-form">
          <div>Gender</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.gender || "-" }}</div>
        </div>
      </div>

      <div class="driver-profile-form-group">
        <div class="driver-profile-form">
          <div>Emergency number</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.emergencyNumber || "-" }}</div>
        </div>

        <div class="driver-profile-form">
          <div>Blood group</div>
          <div class="form-bg" style="text-align: left">{{ addDriverForm.bloodGroup || "-" }}</div>
        </div>
      </div>

      <div class="driver-profile-form-group driver-profile-form" style="flex: 1;">
        <div>Address</div>
        <div class="form-bg">
        <div style="text-align: left; height: 85px; font-weight: 500 !important;">{{ addDriverForm.address || "-" }}</div>
        <div style="text-align: left">City: {{ addDriverForm.city || "-" }}</div>
          <div style="display: flex;">
        <div style="text-align: left; flex: 50%">State: {{ addDriverForm.state || "-" }}</div>
        <div style="text-align: left; flex: 50%">Pincode: {{ addDriverForm.pinCode || "-" }}</div>
          </div>
        </div>
      </div>
    </div>
    <div style="margin-left: 10px; margin-top: 20px;">
      <div>
        <div class="driver-profile-form">
          <div>Aadhaar</div>
          <div style="display: flex;">
            <div><img style="border-radius: 5px;" src="" height="100" width="200" /></div>
            <div><img style="border-radius: 5px; margin-left: 20px;" src="" height="100" width="200" /></div>
          </div>
        </div>

        <div class="driver-profile-form">
          <div>License</div>
          <div style="display: flex;">
            <div><img style="border-radius: 5px;" src="" height="100" width="200" /></div>
            <div><img style="border-radius: 5px; margin-left: 20px;" src="" height="100" width="200" /></div>
          </div>
        </div>
      </div>
      <div></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DriverProfile",
  data(){
    return{
      addDriverForm: {
        emergencyNumber: null,
        bloodGroup: null,
        photo: null,
        firstName: null,
        lastName: null,
        contactNumber: null,
        emp_id: null,
        gender: null,
        dob: null,
        address: null,
        joinedDate: null,
        city: null,
        state: null,
        fleet: null,
        licenseFront: null,
        licenseBack: null,
        pinCode: null,
        acFront: null,
        acBack: null
      }
    }
  },
  computed: {
    getDriverId(){
      return this.$route.params.driverId || null;
    },
    getDriverName(){
      let name = "";
      if(this.addDriverForm.firstName){
        name = this.addDriverForm.firstName;
      }
      if(this.addDriverForm.firstName && this.addDriverForm.lastName) name += " ";
      if(this.addDriverForm.lastName){
        name += this.addDriverForm.lastName;
      }
      return name === "" ? "-" : name;
    }
  },
  methods: {

  },
  mounted() {
      const url = `driver/view?driverId=${this.getDriverId}`;
      fetch(url).then(response => response.json())
          .then(res => {
            this.addDriverForm.firstName = res.driverFisrtName;
            this.addDriverForm.lastName = res.driverLastName;
            this.addDriverForm.dob = res.dob;
            this.addDriverForm.contactNumber = res.phoneNo;
            this.addDriverForm.emp_id = res.emp;
            this.addDriverForm.joinedDate = res.createdDate;
            this.addDriverForm.gender = res.gender;
            this.addDriverForm.address = res.address;
            this.addDriverForm.city = res.city;
            this.addDriverForm.state = res.state;
            this.addDriverForm.fleet = res.fleet;
            this.addDriverForm.pinCode = res.pincode;
          });
  }
}
</script>

